.spacer{
    min-height: 12vh;
}
.container{
    min-height: 88vh;
    margin-left: 18%;
    margin-right: 18%;
}
.upper-part{
    display: flex;
    align-items: center;
    justify-content: center;
}
.profile-img{
    flex-basis: 50%;
}
.info{
    flex-basis: 40%;
}
.profile-img img{
    height: 8rem;
    width: 8rem;
    border-radius: 50%;
}
.profile-videos{
    display: flex;
    flex-wrap: wrap;
}
.profile-videos video{
    height: 20rem;
    padding: 0.5 rem;
}
@media only screen and (max-width: 620px){
    .container{
        min-height: 88vh;
        margin-left: 0;
        margin-right: 0;
        padding: 1rem;
    }
    .upper-part{
        flex-direction: column;
    }
    .profile-videos video{
        width: 100%;
        height: 100%;
    }
    .modal-container{
        display: flex;
        flex-direction: column;
    }
    .modal-container .video-modal{
        width: 100%;
        height: 50%;
    }
    .modal-container .video-modal video{
        width: 100%;
        height: 100%;
    }
    .modal-container .comment-modal{
        width: 100%;
        height: 50%;
    }
}